import React, { Component } from 'react';
import { Link } from 'react-router';

export class Navigation extends Component {
  render() {
    return (
      <div className="navigation">
          <Link className="nav-logo" data-qa="nav-logo" to="/">little moving things</Link>
          <Link className="btn-nav" data-qa="about" to="/about" activeClassName="btn-active">about</Link>
      </div>
  );
  }
}



