import React, { Component } from 'react';
import { stories } from './data/stories';
import { Navigation } from './components/Navigation.js';
import { Footer } from './components/Footer.js';
import { Image } from './components/Image.js';
import { timestampToDate } from './utils/timestampToDate';
import {Link} from 'react-router';
import FontAwesome from 'react-fontawesome';

export class StoryDetailsView extends Component {
  render() {

    const id = this.props.params.id;

    const storyIndex = stories.findIndex(story => story.time === id);

    const story = stories[storyIndex];

    const nextStory = stories[storyIndex-1];
    const nextStoryId = nextStory ? nextStory.time : null;

    const previousStory = stories[storyIndex+1];
    const previousStoryId = previousStory ? previousStory.time : null;

    return (
      <div>
          <Navigation />
          <div className="story-detail">
            <div className="story-detail-left">
              <div className="story-detail-image">
                <Image src={story.url} />
              </div>
              <div className="story-detail-meta">
                <div className="story-detail-date">
                  <p data-qa="story-date">{timestampToDate(story.time)}</p>
                  </div>
                <div className="story-detail-location">
                  <p data-qa="story-location">{story.location}</p>
                  </div>
                </div>
              </div>
            <div className="story-detail-right">
              <div className="story-detail-text">
                  <p data-qa="story-text">{story.text}</p>
              </div>
            </div>
          </div>
          <div className="story-detail-PreNext">
          {previousStoryId && <Link className="btn-previous" data-qa="btn-previous" to={`/stories/${previousStoryId}`}><FontAwesome name="chevron-left"/> Previous story</Link>}
          {nextStoryId && <Link className="btn-next" data-qa="btn-next" to={`/stories/${nextStoryId}`}>Next story <FontAwesome name="chevron-right"/></Link>}
          </div>
          <Footer />
      </div>
    );
  }
}



