import React, {Component} from 'react';
import {createStore, combineReducers, compose, applyMiddleware} from 'redux';
import {Router, Route, browserHistory} from 'react-router';
import {syncHistoryWithStore, routerReducer, routerMiddleware} from 'react-router-redux';
import {Provider} from 'react-redux';
import './styles/styles.css';
import {ConnectedStoryListView} from './StoryListView';
import {StoryDetailsView} from './StoryDetailsView';
import {AboutView} from "./AboutView";
import {analyticsMiddleware} from "./utils/analytics";
import ReactGA from 'react-ga';

const middleware = [routerMiddleware(browserHistory)];

const DEV_TRACKING_ID = 'UA-107816441-1';
const PROD_TRACKING_ID = 'UA-107816441-2';

const isTestEnv = process.env.NODE_ENV === 'test';
const isLocalhost = window.location.hostname === 'localhost';

if (!isTestEnv) {
  const trackingId = isLocalhost ? DEV_TRACKING_ID : PROD_TRACKING_ID;
  ReactGA.initialize(trackingId);
  middleware.push(analyticsMiddleware(ReactGA));
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const reducer = combineReducers({
  routing: routerReducer
});

export class App extends Component {

  constructor(props) {
    super(props);
    this.store = createStore(reducer, composeEnhancers(applyMiddleware(...middleware)));
    this.history = syncHistoryWithStore(browserHistory, this.store);
  }

  render() {
    return (
        <Provider store={this.store}>
          <Router onUpdate={() => window.scrollTo(0, 0)} history={this.history}>
            <Route path="/" component={ConnectedStoryListView}/>
            <Route path="/stories/:id" component={StoryDetailsView}/>
            <Route path="/about" component={AboutView}/>
          </Router>
        </Provider>
    );

  }
}



