import { LOCATION_CHANGE } from 'react-router-redux';

export const analyticsMiddleware = ReactGA => store => next => action => {

  if(action.type === LOCATION_CHANGE) {
    const page = action.payload.pathname;
    ReactGA.pageview(page);
  }

  next(action);

};


