		"text": "Hej! I am a little yellow golf cart type thing! You might think I've crashed into this big pillar but that is not true! My driver has parked me here because I am charging. I am fully electric and that can be a bit tricky here in Stockholm! My batteries last much longer in warmer weather but ah well, the Central station is my playground, so that's not a big problem! I'm charging my batteries here every night in the same spot! Want to have a chat? Come and find me at platform 7-8!",
