		"time": "1460964840",
		"link": "https://www.instagram.com/p/BEVVh5sBfel/",
		"location": "Broadway Market, London",
		"text": "Hello! I am a dark green Mini Clubman Estate! I was born around the 1980s and these days still in perfect condition. Yesterday I was at the lovely sunny Broadway Market for a nice Sunday Roast, Bloody Mary or a Caff Latte, you decide! The good thing is, the weather is getting better. This has 2 perks: more sun and more small cars on the road! Us small cars tend to be a bit of the 'fair weather' kind... We're mostly in hibernation during the winter and come out in spring and summer to drive around and make people happy! So yeah, we're back outside and hoping for an amazing summer!",
