		"location": "Urban Reef, Boscombe Pier, Poole",
		"text": "Hello! I am a little yellow Piaggio Ape, I'm covered in pineapple. Not actual ones but nice icons of pineapples! I'm parked here in front of the Urban Reef restaurant at the Boscombe Pier! Every morning I see walkers, dogs, dog walkers, runners, cyclists and some seagulls doing their thing! Today looks like it's going to be a nice day, so make it a good one! Happy Wednesday!",
