		"location": "Poole",
		"text": "Hello! I am a tiny little motorbike in a shop window here in Poole! You can always find me here at A-Plan Insurance, which can be a bit boring but today been enjoying the spring weather quite a lot! A lot kids come up to the window and ask their mom or dad if they can have me. But their answer is always no! I'd love to have an owner who is a little guy or gal.. That would be brilliant! Little motorbike for a little person!",
