		"location": "Turfveld, Texel",
		"text": "Hallo! I am a little Piaggio Ape Calessino! I live on this beautiful island Texel in the north of the Netherlands. If you're visiting the old harbour in Oudeschild, you'll see that you can hire me for a TukTuk Safari! So come by and hop on!",
