		"text": "Hello! I am a little grey Piaggio Ape! As you can read on my little door and storage box, I belong to this lovely place called @wearefarmstand. We serve nice things that come from good places and are \"free from\" (so no dairy, gluten and no added sugar). Here near Covent Garden I'm currently parked in the shade because the sun was ruining my paint! But today it is a bit breezy so I would actually not mind to be warmed by the sun. But oh well, it's weekend! For us that means more work but thats good for business! Have a good one this weekend!",
