		"text": "Hello! I am a little British racing green Austin Seven! In length I might not be that small but in width I am Tiny! Yes, that's with a capital T because here in Oban, Scotland, they tend to not pronounce the T. So they'll say I'm an \"Aus-in\". Anyway, my boss is a real Scotsman wearing some nice tweed and taking good care of me while touring around the towns and lochs here up north!",
