		"text": "Hallo! I am a tiny yellow BMW 300 Isetta! It is a hot day today in De Bilt at the Art Market, Open Monument Day and Old Timer Day! It is so hot, I opened my one and only door that is located at the front! This makes it so nice and cool but it also attracts a lot of attention. Can you see my steering wheel? It's hanging out! Anyway, stay cool in the shade and have a lovely weekend!",
