		"text": "Hallo! I am a tiny Mango Mobility Napoleon! I'm parked here near the Albert Cuypmarkt in Amsterdam. Yesterday we had a very busy day in the city. The Amsterdam Marathon was on and the weather was beautiful! So that meant lots of runners and even more spectators. \"Fair weather\" spectators I call them. Anyway, you can't say that about me because I'm a tiny, fully electric, one person car that can get you anywhere within 45kms happy and dry. So fair weather or not. I'll get you there all dry!",
