		"text": "Buongiorno! I am a little Fiat 500 parked at the waterfront in the lovely Otranto, Puglia. Today I am dressed up as a wedding-car! My owner just got married to his loved one. And you know what's cool? My interiors are Bordeaux Red, and guess what colour the bride was wearing? Yep, Bordeaux red indeed. She's got taste! I bet she's the one! At the moment they are taking a short break of their busy day and while they are not watching @rcammisola took this quick snap of me. Well, I better get on it! We've got a wedding party to attend!",
