		"location": "Jardim da Torre De Belm, Lisbon",
		"text": "Ol! I am a little fresh Piaggio Ape and today I am feeling IT! It is an AMAZING Wintery day, this means lovely sunshine and a nice fresh temperature. But actually that is not why I am feeling IT today. Its because today I started just like every other day, I serve orange juice fresh from the tree to your glass. It is the best orange juice and its freshly squeezed! All made with oranges from the Algarve, so thats quite local to Lisbon. So if you are not really feeling it today, or any other day, swing by the Jardim da Torre de Belm where youll find me. And after your orange juice fix, youll be on top of it before you know it. If not, you can always climb the Torre de Belm (Belm Tower) which is 30 meters high and is one of the Seven Wonders of Portugal. Its build has been completed in 1519(!) and it gives you an EPIC view. @ollysuss should be able you tell you all about the view because I have never been up there! I might be called Ape but do you really think I can climb on top of an old Tower?",
