		"text": "Hello! I am a little Bedford Bambi! Officially I am called Bambie because my owner thinks that's how it's correctly spelled here in Scotland. Also, my looks are different than my brothers and sisters because I've got my glad rags on, I am ready for tonight's Hogmanay! For the special occasion I've chosen a nature-hippy theme! I'm covered in items that vary from snails  to flowers  and from frozen  branches to some unidentifiable patterns. Anyway, another thing; I'm very honoured to say that I am the #100th #littlemovingthing! Happy new year and on to the next 100 little moving things  #bambie #car #cars #campervan #campervans #scotland #edinburgh #hogmanay #blogmanay #auto #drive #driver #bedford #bedfordbambi #bambi #carstagram #carsofinstagram #campervanlife #paint #graffiti #graffitiart #club100 #100thpost #igerslondon #igers",
