		"location": "Shoreditch, London",
		"text": "Hello, good morning! I am a small frozen Mini Cooper. It's winter here in London and sometimes that means it is minus something-Celsius. You might think \"This casual little Mini doesn't know what cold is!\" Well I do, I was born here in the United Kingdom, but they didn't accept me because I wasn't 'normal'. Well, let me tell you; that is what they say here on this Island. But on mainland Europe, they say I'm completely normal! So yes, I went to experience the cold winter months in Bremen. It can be pretty chilly out there! Now I'm back here where it all started and I feel I'm being accepted more. Sometimes they do call me righty but ah well, that's a good nickname!",
