		"location": "Forth & Clyde Canal, Glasgow",
		"text": "Hello! I am a little narrow blue boat called Moles Boat! I'm docked here in the Forth and Clyde canal in Glasgow at the top of Maryhill. I've got a small out-boat motor instead of an indoors one. You can probably guess why! I'm little! Not enough space. It also means that the egg hunt that is planned for this week will be relatively short! There is not a lot of space to search for eggs! But let that not ruin your weekend, Happy Easter! Cheerio!",
