		"location": "Westfield Stratford City, London",
		"text": "Hello! I am a little Westfield Centre Rover! I've got quite a relaxed live, I'm roving around Westfield Stratford all the time. I does get quite hectic here, that why parents decide to take me for a ride! They then put the worst drivers in the world behind the steering wheel but actually that doesn't matter. I'm already happy if the little versions of the parents aren't screaming, fighting, sick or pretending my two steering wheels really work! Because they are just there for show and entertainment, nothing more. And actually, I've got 4 wheels but a drivetrain of 0x0. They don't even touch the ground! Does that mean I'm hovering?",
