		"location": "Petersen Automotive Museum, Los Angeles",
		"text": "Hello! I am a little Bugatti Type 52, also known as a Baby Bugatti. I'm a small kids car designed by Ettore Bugatti in 1927. I'm kitted out with a 12V. That's a pretty good setup, I have a blistering top speed of about 20 km/h. To be clear, it's a 12V battery, unfortunately not a V12 engine. Some of my older brothers and sisters had a thermic motor but Mr. Bugatti had to change the design to fit kids older than 6! Anyway, I'm hanging out here at 'The Art of Bugatti' exhibition at the @petersenmuseum in LA. The special exhibition is pretty great so come and say Hi!",
