		"location": "Petersen Automotive Museum, Los Angeles",
		"text": "Hello! I am a little Indian MM5A otherwise known as Mini Mini Bambino. I was born in 1969 and I've always been 18\" high with an 50cc de-tuned engine to 10-12 mph, that's to keep your kids safe. I was created for the 4 to 8 year olds of this world and back in the days advertised with \"If your son can't pedal a bicycle, put him on an Indian mini mini Bambino\". I'm as they call it a mini-bike. The first mini-bikes were used by people who would need to race around the pits on a race track . To sell them to everyone in the 1960s and to introduce motorcycles to the next generation, they created various ads. One featuring the text \"Nun like it.\" underneath that line, a photo of a nun racing on a mini-bike. That's quite out there isn't it?",
