		"location": "Starbucks 4th Street and Market, San Francisco",
		"text": "Hello! I'm a SFPD G04 Interceptor here in front of Ross Dress for Less. There has been a crime here in the neighbourhood and that's why I'm on the scene. Have you seen what's inside that shop? Well, that's a crime. Shouldn't even be for sale, lol. Anyway, back to the serious matter. I'm here in the burning sun because the weather people got it wrong today. They said 12 degrees Celsius and cloudy, but it turned out to be HOT AND ROASTING without many clouds. In the end that was pretty good because I ended up here for the rest of the days. My huge brothers and sisters over at @hugemovingthings had a more livelier day, so check them out!",
