		"text": "Hallo! I am a red little tractor!  Remember me? @foodlesss surely did! Last time she saw me here, I was carrying a lot of old bikes, metal scrap and other stuff in my trailer. That was around Christmas time in 2014, when everything was all good. Now, I am not feeling so well. I don't have a trailer anymore so I am a bit lonely and to make things worse, my big rear tire has gone flat. Do you know how much air that normally holds? Yes, a lot indeed. Almost twice the air my front wheels hold but now it is not holding any. Anyway, I hope my boss comes and fixes me up pretty soon because some one has to drive around Utrecht and collect the metal scrap! I don't even want to think about what will happen when I don't get fixed ",
