		"text": "Hallo! It is me again, the red little tractor! Live from Utrecht it is @foodlesss who came to interview me. I have good news and bad news. First the good news, my boss gave me a little jack, so that I'm a bit more straight and upright. The bad news is, he's taken off my wheel. I feel so naked right now! My insides are being shown to the outer world. The boss assured me that my wheel will be back soon and will be as good as new, in other words, it is being repaired. More good news, I have found my little trailer again. It's right behind me but just outside of the picture frame! It's currently full of scrap metal (not my wheel) but it will have to wait to be emptied, because first I need my wheel back!",
