		"location": "The Stadium, Queen Elizabeth Olympic Park, London",
		"text": "Hello! I am a little golfcar turned helper at the Athletics World Championships. Today is the last day of the tournament and in the past weeks I've seen loads of new and old champions! Currently I am carrying two big bits of the pole vaulting mattresses that go underneath the bar the athletes need to jump over! It's pretty epic, to see them fly so high. I wouldn't want to do that, I'm scared of heights! Just keep me on the track, which actually is a pretty great track! Did you see the GB team take Silver (women) and Gold (men) in the 4x100m relay? And Dutch lady Schippers win in the 200m? It's been a pretty great tournament for most, unfortunately not for the living legend Usain Bolt . It wasn't his tournament but his achievements from previous years will live on and on.",
