		"text": "Hola! I am a Aixam 500 Super Luxe! I'm of the quadricycle kind, that category is also named microcar. I've told this same story to these two lads here and they immediately decided to see if they could lift me up! Meanwhile, @jacek_rzrz snapped a wonderful series of photos so that he had evidence of this abuse! I'm here in the small town San Isidro, in Alicante, Spain. Most tourists that come here are on holiday to chill and enjoy the nearby beaches but no, these came here for a weekend to do lots of exercise (including lifting a small car) and eat nice foods!",
