import React, { Component } from 'react';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import { stories } from './data/stories.js';
import { Navigation } from './components/Navigation.js';
import { Footer } from './components/Footer.js';
import { Image } from './components/Image.js';

export class StoryListView extends Component {

  renderStory(story) {
    const handleClick = () => this.props.navigateToStory(story);
    return (
      <div className="story-thumbnail" key={story.time}>
        <Image src={story.url} onClick={handleClick} />
      </div>
    );
  }

  renderStories() {
    return (
      <div className="stories-wrapper">
        { stories.map(story => this.renderStory(story)) }
      </div>
    );
  }

  render() {
    return (
      <div>
            <Navigation />
            { this.renderStories() }
            <Footer />
      </div>
    );
  }
}

const mapStateToProps = () => ({});

const mapDispatchToProps = dispatch => ({
  navigateToStory: story => dispatch(push(`/stories/${story.time}`))
});

export const ConnectedStoryListView = connect(mapStateToProps, mapDispatchToProps)(StoryListView);



