import React, {Component} from 'react';
import {Navigation} from './components/Navigation.js';
import {Footer} from './components/Footer.js';

export class AboutView extends Component {
  render() {

    return (
        <div>
          <Navigation/>
          <div className="about" data-qa="about-text">
            <p data-qa="greeting" className="greeting">Hello! Hola! Salut! Hej! Ol! Hallo!</p>
            <p data-qa="p1">little moving things is a collection of photos and short stories, taken or curated and
              written by Jesper Brring. The stories are based on true facts or false information.</p>
            <p data-qa="p2">It all started on a holiday in Sicily, in the streets of Catania to be precise. I found
              loads of little cars, mostly Piaggio Apes, some with more character than others. The more I found, the
              more pictures I took and information I looked up about the little cars. This transformed into short
              stories where the moving things can air their thoughts, life story or anything else.</p>
            <p data-qa="dev-webapp">This website is a React app build with <a
                href={"https://github.com/facebookincubator/create-react-app"} target={"_blank"}>Create React App</a> and with lots of help from engineer <a
                  href={"https://uk.linkedin.com/in/jacekrzrz"} target={"_blank"}>Jacek Rzeniewicz.</a></p>
            <p data-qa="bye">Enjoy!</p>
          </div>

          <Footer/>
        </div>
    )
  }
}



