import React, { Component } from 'react';

export class Image extends Component {
  render() {
    let { src, onClick } = this.props;
    //let { src, ...props } = this.props;
    var dpr = window.devicePixelRatio || undefined;
    var width = 0;
    // '&dpr='+window.devicePixelRatio+


    if (this.element) {
      width = '&w='+this.element.getBoundingClientRect().width+'&h='+this.element.getBoundingClientRect().width;
    } else {
      src = null;
    }

    if (window.devicePixelRatio) {
      dpr = '&dpr='+window.devicePixelRatio;
      src = src+'?fit=max'+width+dpr;
    } else {
      src = src+'?fit=max'+width;
    }

  const backgroundImage ={backgroundImage: `url(${src})`};
    return (
        <div className="image-wrapper" style={backgroundImage} data-qa={src} ref={element => this.element = element} onClick={onClick} />
      );
  }

  componentDidMount() {
    this.setState({ imageHasRendered: true });
  }
}



